@import "../../../shared/style/base";
@import "../../../shared/style/styles";

.footer {
  background: $footer-bg-color;
  padding: 20px;
  &Content {
    @include flex(space-between, center);
  }

}

.iconViber {
  fill: white;
  width: 25px;

  &:hover, &:focus {
    fill: $icon-accent-color;
  }
}

.socialList {
  @include flex(_, center);
  list-style: none;
}

.socialListItem {
  :hover {
    color: white;
    font-size: 16px;
  }

  &:not(:last-child) {
    margin-right: 15px;
  }
}