@import "../../../shared/style/base";
@import "../../../shared/style/styles";

.icon{
  width: 25px;
  fill: $white-color;
  .contactListItemLink:hover & {
    fill: $icon-accent-color;
  }
}
.contactListItem{
  &:not(:last-child){
    margin-right: 15px;
  }
}
.contactListItemLink{
  @include flex();
  text-decoration: none;
  color: $white-color;
  font-style: italic;
  font-size: 12px;

  &:hover{
    color: $icon-accent-color;
    font-style: italic;
  }
}