@import "../../../shared/style/base";
@import "../../../shared/style/styles";


.slider {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  //padding: 40px 0;
}

.sliderContent {
  position: relative;
}

.image {
  width: 100%;
  height: 350px;

  @media screen and (min-width: 768px) {
    height: 600px;
  }
}

.prevImg {
  padding: 0;
  border: none;
  background: transparent;
  position: absolute;
  top: 170px;
  z-index: 10;
  cursor: pointer;

  &:hover {
    background-color: rgba(0, 0, 0, 0.5);
  }
}

.svgBtnIconPrev {
  width: 42px;
  height: 42px;

  .prevImg:hover & {
    stroke: white;
  }
}

.nextImg {
  padding: 0;
  border: none;
  background: transparent;
  position: absolute;
  z-index: 10;
  top: 170px;
  right: 0px;
  cursor: pointer;

  &:hover {
    background-color: rgba(0, 0, 0, 0.5);
    color: darkorange;
  }
}

.svgBtnIconNext {
  width: 42px;
  height: 42px;

  .nextImg:hover & {
    stroke: white;
  }
}

.contentSliderBox {
  color: white;
  background: rgba(0, 0, 0, .4);
  padding: 15px;
  border-radius: 5px;
  position: absolute;
  bottom: 10px;
  left: 0px;

  &Caption {
    margin: 0;
  }

  &Text {
    margin: 12px 0;
  }
}
