@import "../../../shared/style/base";
@import "../../../shared/style/styles";

.btnSubscribe{
  background: $primary-btn-bg-color;
  color: $white-color;
  box-shadow: 0px 9px 48px rgba(24, 220, 109, 0.15);
  border-radius: 43px;
  padding: 11px 31px;
  border: none;

  &:hover{
    background: $white-color;
    cursor: pointer;
    color: $icon-accent-color;
    border: 1px solid $icon-accent-color;
    box-shadow: 0px 0px 20px $icon-accent-color;
  }
}